
import NextParticle from '../nextparticle.js'

import {useEffect, useRef,useState} from 'react';

export default function Particle(props) {
  const imgRef = useRef(null);
  const container = useRef(null);
  // const [show,setShow] = useState('block');
  let nextParticle;
  useEffect(() => {
    nextParticle = new NextParticle(imgRef.current);
    console.log('start')
    
    nextParticle.width = container.clientWidth;
    nextParticle.height = container.clientHeight;
    setTimeout(()=>{nextParticle.start()},1000)
    window.onresize = function() {
      nextParticle.width = container.clientWidth;
      nextParticle.height = window.clientHeight;
      // nextParticle.start();
    };
    // nextParticle._animate();
    // return () => {
    //   nextParticle.stop();
    // }
  }, [])

  return <div ref={container}>
       <img
       ref={imgRef}
      data-particle-gap="2"
      // data-disable-interaction="true"
      data-gravity="0.1"
      data-noise="1"
      src={props.src}
      data-init-position="top"
      data-init-direction="top"
      data-fade-position="bottom"
      data-fade-direction="bottom"
      data-width="500"
      data-height="700"
      // data-color="#fff"
      
      // width={Math.max(props['data-width'],props['data-height'])}
      // height={Math.max(props['data-width'],props['data-height'])}
      style={{display:'none'}}
      // {...props}
    />
    </div>  
}